<template>
  <div id="player">
    <h2 class="title">利明音乐</h2>
    <div class="search">
      <input type="text" v-model.trim="searchName" @keyup.enter="sendSearch" />
      <button>
        <span class="iconfont icon-search" @click="sendSearch"></span>
      </button>
    </div>
    <div class="tab-wrapper">
      <!-- 对应的内容区域 -->
      <div class="tab-content">
        <router-view :sonchange="sonchange"></router-view>
      </div>
      <div>
        <aplayer
          showLrc
          :music="{
          title: songname,
          artist: who,
          src: nowplaySongs,
          pic: imgurl,
          lrc : geci}"
          
        />
      </div>
    </div>
  </div>
</template>

<script>
import Aplayer from "vue-aplayer";
export default {
  data() {
    return {
      searchName: "",
      geci:'',
      imgurl: '',
      songname:'我可以',
      who : '蔡铭佑',
      nowplaySongs:"http://m7.music.126.net/20190722205304/429ac210129fe16aca309ad6d39ff9c1/ymusic/0f53/525b/015c/898a2f6907e10c8be4fe668f4b52f39f.mp3"
    };
  },
  components: {
    Aplayer
  },
  methods: {
    // 1. 发送搜索信息
    sendSearch() {
      // 1. 发送当前的数据子路由组件result中
      this.$router.push(`/results/?searchName=${this.searchName}`);
      // 2. 清空当前的文本框数据
      this.searchName = "";
    },

    // 2. 传递给子组件调用
    sonchange(url,name,who,geci,imgurl){
      this.nowplaySongs = url;
      this.songname = name;
      this.who = who;
      this.geci = geci;
      this.imgurl = imgurl;

    }
  },
  
};
</script>

<style scoped lang="less">
@import url("./assets/css/index.css");
@import url("./assets/css/iconfont.css");
</style>
