<template>
  <div class="result-wrapper">
    <div
      class="song"
      v-for="(ele, index) in songsLists"
      @click="getSong(ele.id,ele.name,getSongFrom111(ele.artists))"
      :key="index"
    >
      <div class="name">
        <span class="iconfont icon-play"></span>
        {{ele.name}}
        <span class="iconfont icon-editmedia"></span>
      </div>
      <div class="singer">{{ele.artists|getSongFrom}}</div>
      <div class="album">{{ele.album.name}}</div>
      <div class="time">{{ele.duration|filtertime}}</div>
    </div>
    <div class="posPagination">
      <el-pagination
        background
        :page-size="10"
        @current-change="getpage"
        layout="prev, pager, next, total, jumper"
        :total="allSongAll"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import { log } from "util";
export default {
  data() {
    return {
      // 1. 歌曲名字
      defaultSong: "我可以",
      // 2. 歌曲列表
      songsLists: [],
      // 3. 总的歌曲数量
      allSongAll: 0,
      // 4. 现在播放的url
      playnowurl: "111"
    };
  },
  created() {
    // 2. 获取当前列表
    this.getSonsLists(undefined, 1);
  },
  props: {
    sonchange: Function
  },
  methods: {
    // 1. 获取歌曲列表
    getSonsLists(songName, page) {
      // 1.1 判断是否是第一次
      if (songName == undefined) {
        songName = this.defaultSong;
      }
      // 1.2 发送请求,获得数据
      this.axios
        .get(`/search?keywords=${songName}&limit=10&offset=${page}`)
        .then(backdata => {
          this.songsLists = backdata.data.result.songs;
          this.allSongAll = backdata.data.result.songCount;
        });
    },
    // 2.分页器点击分页
    getpage(num) {
      this.getSonsLists(this.$route.query.searchName, num);
    },
    // 3. 过滤歌曲
    getSongFrom111(value) {
      // 遍历数组.取值
      let name = "";
      value.forEach((ele, index) => {
        if (index == value.length - 1) {
          name += ele.name;
        } else {
          name += ele.name + " | ";
        }
      });
      return name;
    },
    // 4. 点击获取音乐信息
    async getSong(id, name, who) {
      // 4.1 获取歌词
      let geci = await this.axios.get(`/lyric?id=${id}`);
      geci = geci.data.lrc.lyric;

      // 4.2 获取歌曲的背景图片
      let imgurl = await this.axios.get(`/song/detail?ids=${id}`);
      console.log(imgurl);
      imgurl = imgurl.data.songs[0].al.picUrl
      
      // 4.3 获取歌曲的url
      this.axios.get(`/song/url?id=${id}`).then(backdata => {
        this.sonchange(backdata.data.data[0].url, name, who, geci, imgurl);
      });
    }
  },
  watch: {
    // 1. 监听上头新传过来的搜索关键字
    "$route.query.searchName"() {
      this.getSonsLists(this.$route.query.searchName, 1);
    }
  },
  filters: {
    // 1. 过滤明星名字
    getSongFrom(value) {
      // 遍历数组.取值
      let name = "";
      value.forEach((ele, index) => {
        if (index == value.length - 1) {
          name += ele.name;
        } else {
          name += ele.name + " | ";
        }
      });
      return name;
    },
     // 2. 过滤时间
  filtertime(value) {
    value = parseInt(value/1000)
    var M = Math.ceil(value/60)
    M = M.toString().padStart(2,'0');
    var S = Math.ceil(value%60);
    return M+':'+S
  }
  }
 
};
</script>

<style scoped lang="less">
.posPagination {
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>
